import React from 'react'
import PropTypes from 'prop-types'
import moment from 'moment'
import 'moment-timezone'

import {
  CartesianGrid,
  Legend,
  ResponsiveContainer,
  Tooltip,
  AreaChart,
  Area,
  LineChart,
  Line,
  XAxis,
  YAxis,
} from 'recharts'

function formatXAxis(tickItem){
    var date = new Date(tickItem).toISOString();
    return moment(date).utcOffset('-0400').format('hh:mm');
}

const TimeSeriesChart = ({ chartData }) => (
    <ResponsiveContainer width="95%" height="85%">
        <AreaChart data={chartData}
            margin={{top: 5, right: 30, left: 20, bottom: 5}}>
      <XAxis
        dataKey = 'date'
        domain = {['auto', 'auto']}
        name = 'Time'
        tickFormatter = {(date) => formatXAxis(date)}
      />
        <YAxis/>
        <CartesianGrid strokeDasharray="3 3"/>
        <Tooltip/>
        <Legend />
        <Area type="monotone" dataKey="heart_rate" stroke="#2C839F" fill='#2C839F'  activeDot={{r: 8}}/>
        <Area type="monotone" dataKey="oxygen_level" stroke="#8884d8" fill='#8884d8'  activeDot={{r: 8}}/>
        </AreaChart>
    </ResponsiveContainer>
)

TimeSeriesChart.propTypes = {
  chartData: PropTypes.arrayOf(
    PropTypes.shape({
      time: PropTypes.number,
      value: PropTypes.number
    })
  ).isRequired
}

export default TimeSeriesChart